import ImagesAPI from '@/services/ImagesAPI';

const clientId = '88f81e2335d1a4d3ea1815f1f10603ab9b1ada36de51484f4e328d5c94bec9e0';
export default {
  getRandom() {
    // return Api().get(`id=${params}&APPID=${apiKey}`);
    return ImagesAPI.get(`/photos/random?orientation=landscape?client_id=${clientId}`);
  },
  getRandomByWeather(params) {
    // return Api().get(`id=${params}&APPID=${apiKey}`);
    return ImagesAPI.get(`/photos/random?orientation=landscape&query=${params.type}?client_id=${clientId}`);
  },
};



